                                </div>
                            </div>
                            <div class="col-lg-4 order-lg-3 text-lg-right align-self-lg-center">
                                <div class="card-profile-actions py-4 mt-lg-0">
                                    <base-button type="info" size="sm" class="mr-4">Add as friend</base-button>
                                    <base-button type="default" size="sm" class="float-right">Block</base-button>
                                </div>
                                <div>
                                    <span class="align-top mr-2">toggle rx:</span>
                                    <base-switch id="input-sm" v-model="rxStat" size="sm"></base-switch>
                                </div>
                            </div>
                            <div class="col-lg-4 order-lg-1">
                                <div class="card-profile-stats d-flex justify-content-center">
                                    <div
                                        v-for="(v,k) in {
                                            pp: autoToggleRxUser[strMode].pp, 
                                            accuracy: smallerFloatHtml(`${autoToggleRxUser[strMode].accuracy.toFixed(2)}%`),
                                            'Play Count': autoToggleRxUser[strMode].playcount,
                                            //TTH: autoToggleRxUser[strMode].total_hits,
                                            }"
                                        :key="`user-stat-${k}`"
                                    >
                                        <span class="heading" v-html="v"></span>
                                        <span class="description" v-html="k"></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="text-center mt-5">
                            <h3>
                                {{autoToggleRxUser.username}}
                                <small
                                    class="font-weight-light"
                                >, #{{autoToggleRxUser[strMode].global_leaderboard_rank || 0}}</small>
                            </h3>
                            <div class="h6 font-weight-300">
                                <i class="ni location_pin mr-2"></i>
                                <gb-flag
                                    :code="autoToggleRxUser.country"
                                    size="small"
                                    iconPath="assets/flags"
                                />
                                #{{autoToggleRxUser[strMode].country_leaderboard_rank || 0}}
                            </div>
                            <div class="h6 mt-4">
                                <i class="ni business_briefcase-24 mr-2"></i>
                                Last seen at {{moment(autoToggleRxUser.latest_activity).fromNow()}}
                            </div>
                            <div>
                                <i class="ni education_hat mr-2"></i>
                                registered at {{moment(autoToggleRxUser.registered_on).format('LLLL')}}
                            </div>
                        </div>
                        <div class="mt-5 py-5 border-top text-left">
                            <p v-html="userpage"></p>
                            <!-- <div class="row justify-content-center text-center">
                                <div class="col-lg-9">
                                    <a href="#">Show more</a>
                                </div>
                            </div>-->
                            <b-tabs v-model="numericMode" class="nav-wrapper" pills fill>
                                <b-tab>
                                    <span slot="title" class="nav-link-icon d-block">std</span>
                                </b-tab>
                                <b-tab>
                                    <span slot="title" class="nav-link-icon d-block">taiko</span>
                                </b-tab>
                                <b-tab>
                                    <span slot="title" class="nav-link-icon d-block">catch</span>
                                </b-tab>
                                <b-tab>
                                    <span slot="title" class="nav-link-icon d-block">mania</span>
                                </b-tab>
                            </b-tabs>
                        </div>
                    </div>
                </card>
            </div>
            <div class="container" v-else>
                <article>user not exists</article>
            </div>
        </section>
        <section class="section section-shaped mt-0" v-if="autoToggleRxUser.id">
            <div class="shape shape-style-1 bg-gradient-primary shape-skew">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="container">
                <bp-card :id="id" :rx="rxStat" :mode="numericMode" />
            </div>
        </section>
        <section class="section section-shaped my-0" v-if="autoToggleRxUser.id">
            <div class="shape shape-style-1 bg-gradient-warning shape-skew">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="container">
                <recent-card :id="id" :rx="rxStat" :mode="numericMode" />
            </div>
        </section>
    </div>
</template>
<script>
const moment = require("moment");
const axios = require("axios");
// const BBCodeParser = require("bbcode-parser");
// const parser = new BBCodeParser(BBCodeParser.defaultTags());

import bbCodeParser from "js-bbcode-parser";
const defaultCodes = [...bbCodeParser.codes];

import GoTop from "@inotom/vue-go-top";
import BpCard from "./components/sb/BPCard";
import RecentCard from "./components/sb/RecentCard";

import Tabs from "@/components/Tabs/Tabs.vue";
import TabPane from "@/components/Tabs/TabPane.vue";

// const { default: VueJsonPretty } = require("vue-json-pretty");
import VueJsonPretty from "vue-json-pretty";

export default {
    data: () => ({
        user: {},
        userRx: {},
        userpageRaw: "",
        userpage: "",
        id: -1,
        selectedMode: 0,
        logger: false
    }),
    props: {
        handle: {
            type: String,
            default: -1
        },
        mode: {
            type: String,
            default: "std"
        },
        rx: {
            type: String,
            default: ""
        }
    },
    beforeCreate() {
        moment.locale(this.$root.locale || "zh-cn");
        const l2Parser = {};
        l2Parser.__proto__ = bbCodeParser.__proto__;
        l2Parser.setCodes({
            "\\[center\\](.+?)\\[/center\\]":
                '<div class="text-center">$1</div>'
        });
        const newCodes = l2Parser.codes;
        bbCodeParser.codes = [...defaultCodes, ...newCodes];
    },
    computed: {
        numericMode: {
            get() {
                switch (this.mode) {
                    case "std":
                    case "osu":
                        return 0;
                    case "taiko":
                        return 1;
                    case "catch":
                    case "ctb":
                        return 2;
                    case "mania":
                        return 3;
                    default:
                        return 0;
                }
            },
            set(newVal) {
                // this.log({newVal},"switch mode");
                this.$router.push({
                    path: `/users/${this.id}/${this.toStrMode(newVal)}/${
                        this.rx
                    }`
                });
            }
        },
        autoToggleRxUser: {
            get() {
                return this.rxStat ? this.userRx : this.user;
            },
            set(user) {
                if (this.rxStat) {
                    this.userRx = user;
                } else {
                    this.user = user;
                }
            }
        },
        strMode() {
            return this.toStrMode();
        },
        rxStat: {
            get() {
                return Boolean(this.rx == "rx");
            },
            set(newVal) {
                if (typeof newVal == "object") return;
                const path = `/users/${this.id}/${this.strMode}${
                    newVal ? "/rx" : "/"
                }`;
                this.log({ newVal, path }, "toggle");
                this.$router.push({
                    path
                });
            }
        }
    },
    mounted() {
        this.log({
            handle: this.handle,
            mode: this.mode,
            rx: this.rxStat,
            numericMode: this.numericMode
        });
        this.init();
    },
    components: {
        BpCard,
        RecentCard,
        GoTop,
        VueJsonPretty,
        Tabs,
        TabPane
    },
    methods: {
        async init() {
            // this.log(this.$route.params, "router params");
            const apiBase = this.$root.apiBase;
            // const userId = this.id || 1001;
            if (!isNaN(this.handle)) this.id = parseInt(this.handle);
            else {
                const result = await axios({
                    url: `${apiBase}/users/whatid`,
                    params: {
                        name: this.handle
                    }
                })
                    .then(res => res.data)
                    .catch(err => false);
                if (!result) return;
                else this.id = result.id;
            }
            // this.log(this.id, "final id");
            if (this.id !== this.autoToggleRxUser.id) {
                this.userpageRaw = "";
                this.userpage = "";
                const user = await axios({
                    url: `${apiBase}/users/${this.rxStat ? "rx" : ""}full`,
                    params: {
                        id: this.id
                    }
                }).then(res => res.data);
                const userpage = await axios({
                    url: `${apiBase}/users/userpage`,
                    params: {
                        id: this.id
                    }
                }).then(res => res.data);
                // console.log(user, userpage);
                this.autoToggleRxUser = user;
                // this.log(user, "user");
                this.userpageRaw = userpage.userpage;
                this.userpage = this.convertBBCode(this.userpageRaw);
            }
        },
        moment(...any) {
            return moment(...any);
        },
        convertBBCode(bbcode) {
            bbcode = bbcode.split("\n").join("<br>");
            bbcode = this.replaceEmoji(bbcode);
            // console.log(bbCodeParser.codes);
            bbcode = bbCodeParser.parse(bbcode);
            // console.log(bbcode);
            // bbcode = bbCodeParser.parse(bbcode);
            // bbcode = l2Parser.parse(bbcode);
            return bbcode;
        },
        replaceEmoji(html) {
            Object.entries({
                ":peppy:": '<img src="./static/emotes/peppy.png">',
                ":barney:": '<img src="./static/emotes/barney.png">',
                ":akerino:": '<img src="./static/emotes/akerino.png">',
                ":foka:": '<img src="./static/emotes/foka.png">',
                ":kappy:": '<img src="./static/emotes/kappy.png">',
                ":creepypeppy:": '<img src="./static/emotes/creepypeppy.png">',
                ":peppyfiero:": '<img src="./static/emotes/peppyfiero.png">',
                ":djpeppy:": '<img src="./static/emotes/djpeppy.png">',
                ":kappa:": '<img src="./static/emotes/kappa.png">'
            }).map(
                ([occur, replace]) => (html = html.split(occur).join(replace))
            );
            return html;
        },
        toStrMode(numericMode = this.numericMode) {
            switch (numericMode) {
                case 0:
                    return "std";
                case 1:
                    return "taiko";
                case 2:
                    return "catch";
                case 3:
                    return "mania";
                default:
                    break;
            }
        },
        smallerFloatHtml(str) {
            const splitted = str.split(".");
            return `${splitted[0]}<small>.${splitted[1]}</small></span>`;
        },
        createTitle(message) {
            // Use a shorter name for this.$createElement
            const h = this.$createElement;
            // Create the title
            const vNodesTitle = h("strong", { class: "mr-2" }, message);
            return vNodesTitle;
        },
        createJSONViewer(message) {
            // const h = this.$createElement;
            // // Create the message
            // const vNodesMsg = ()
            // return vNodesMsg
            return <VueJsonPretty data={message}></VueJsonPretty>;
        },
        popToast(vNodesMsg, vNodesTitle, { variant = "info", toster } = {}) {
            // Pass the VNodes as an array for message and title
            return this.$bvToast.toast([vNodesMsg], {
                title: [vNodesTitle],
                solid: true,
                variant: variant
                // toster
            });
        },
        async log(e, title = "result", { variant = "info" } = {}) {
            if (!this.logger) return;
            const safeStringify = (obj, indent = 2) => {
                let cache = [];
                const retVal = JSON.stringify(
                    obj,
                    (key, value) =>
                        typeof value === "object" && value !== null
                            ? cache.includes(value)
                                ? undefined // Duplicate reference found, discard key
                                : cache.push(value) && value // Store value in our collection
                            : value,
                    indent
                );
                cache = null;
                return retVal;
            };
            return this.popToast(
                this.createJSONViewer(JSON.parse(safeStringify(e))),
                this.createTitle(title),
                {
                    variant
                }
            );
        }
    },
    watch: {
        $route(to, from) {
            this.init();
        }
        // selectedMode(newVal) {

        // }
    }
};
</script>
